  background-image: url('bg-img4.jpg');
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.bg-oscuro {
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.header, .footer {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  align-items: center;
}

.form-fields {
  background-color: rgba(255, 255, 255, 0.7);
  margin: 10px auto;
  border-radius: 5px;
  color: black;
  padding: 5px 20px;
  width: 50%;
}

@media (max-width: 650px) {
  .form-fields {
    width: 95%;
  }
}
@media (max-width: 900px) {
  .form-fields {
    width: 80%;
  }
}
.form-header {
  text-align: center;
}

.form-header p, .form-header h1 {
  margin: 10px;
  padding: 0;
}

.form-header h1 {
  font-weight: bold;
  font-size: 24px;
}

.form-header p {
  font-weight: normal;
  font-size: 16px;
  margin-bottom: 25px;
}

.form-inputs ion-input {
  border: 1px solid black;
  margin: 8px auto;
  border-radius: 5px;
  font-size: 14px;
}

.forgot-pass {
  font-size: 10px;
  font-weight: lighter;
  margin: 0px;
  padding: 0;
  padding: 0px;
  width: 100%;
}

.form-buttons {
  width: 100%;
  margin: 0;
  margin-top: 25px;
  margin-bottom: 10px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-buttons ion-button {
  width: 18rem;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

#logo {
  height: 75%;
}

.header {
  position: absolute;
  top: 0px;
  height: 275px;
}

.footer {
  height: 30px;
  position: absolute;
  bottom: 0px;
  background-color: rgba(255, 102, 0, 0);
}

.buttons {
  font-size: 12px;
}

ion-button#help {
  --border-radius: 50%;
}

ion-label {
  padding: 0;
}

.hyper {
  color: #ff5100;
  font-weight: bold;
